.Vote{
    background: #3f51b5;
    height: 100vh;
    width: 100vw;
    display: table-cell;
    text-align: center;
    vertical-align: middle;
}

.voteWindow{
    background: white;
    border-style: solid;
    border-width: 0px;
    border-radius: 5px;
    box-shadow: #000000;
    display: inline-block;
    padding: 15px;
    text-align: left;
}